body {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.column {
  /* display: flex;
  flex-direction: column; */
}

/* NavBar */
.navz {
  z-index: 3;
  }

.headpadding {
  width: 100%;
  height: 70px;
}

.centered2 {
  position: absolute;
  text-align: center;
  margin: auto;
  /* padding-top: 60px; */
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0%);
}

.nav-scrolled {
  background-color: rgb(0, 0, 0, 1);
  width: 100%;
  display: flex;
  margin: 0;
  padding: 0;
  z-index: 3;
  position: fixed;
  top: 0;
  text-align: center;
  margin: 0 auto;
  align-content: center;
  align-items: center;
  align-self: center;
  justify-content: center;
  height: 70px;
}

.nav {
  background-color: black;
  width: 100%;
  display: flex;
  margin: 0;
  padding: 0;
  z-index: 3;
  opacity: 0.9;
  text-align: center;
  margin: auto;
  align-content: center;
  align-items: center;
  align-self: center;
  justify-content: center;
  height: 70px;
  transition: 0.2s;
}

.link {
  text-decoration: none;
  font-weight: 300;
  color: white;
  width: 150px;
  height: 30px;
  padding: 20px 0px 20px 0px;
  text-align: center;
  font-size: 20px;
  transition: 0.5s;
}

.link:hover {
  cursor: -webkit-grab;
  cursor: grab;
}

.active {
  color: gray;
  background-color: black;
  cursor: -webkit-grab;
  cursor: grab;
}

.nav_underliner {
  position: relative;
}

.link:hover .nav_underliner:before {
  visibility: visible;
  transform: scaleX(1);
}

.nav_underliner:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  bottom: -25px;
  left: 0%;
  background-color: white;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out;
}

